/* eslint-disable */
import React, { FC, useState, useEffect } from 'react';
import { List, Checkbox, Radio, InputItem, DatePicker, Toast, ImagePicker, Button, Picker, Modal, Popover, TextareaItem } from 'antd-mobile';
import { SupNavBar, SupLabel, SupItem, AdpOrgRefrence, SupTreePicker, SupNavBarProps } from '@sup-mobile/ui';
import { dateFormat, GetRequest, dataURLtoBlob } from '../../../util/common';
import { createForm } from 'rc-form';
import './index.less';

export interface FormDemoProps {
  some: string;
}
const ReleaseAction: FC<any> = (props: any) => {

  let requestParams = GetRequest(window.location.href);

  const [isShowAll, setIsShowAll] = useState<any>(true);


  const [actionItemData, setActionItemData] = useState<any>({});

  // 刷新
  const fillProps: SupNavBarProps = {
    mode: 'fill',
    onLeftClick: () => {
      window.location.href = `#/demo/eventEditing?id=${requestParams.id}&tabIndex=${1}`;
      if (Object.keys(actionItemData).length > 0) {
        sessionStorage.removeItem('SESActionItem');
      }
    },
  };

  const initData = async () => {
    let actionItem: any = {};
    if (JSON.parse(sessionStorage.getItem('SESActionItem') as any) !== undefined && JSON.parse(sessionStorage.getItem('SESActionItem') as any) !== null && JSON.parse(sessionStorage.getItem('actionItem') as any) !== '') {
      actionItem = JSON.parse(sessionStorage.getItem('SESActionItem') as any);
      setActionItemData(actionItem);
    }
  }

  useEffect(() => {
    initData();
  }, []);


  return (
    <div className="releaseAction-root">
      <div className='releaseAction-root-header'>
        <SupNavBar  {...fillProps}>应急行动</SupNavBar>
      </div>
        <div className='releaseAction-title'>
          <div>基础信息</div>
          <div className={isShowAll ? 'arrow' : 'notAll'} onClick={() => setIsShowAll(!isShowAll)}></div>
        </div>
        <div className={isShowAll ? 'releaseAction-content' : 'releaseAction-hidden'}>
          <div>
            <div>简述</div>
            <div>{actionItemData.describe}</div>
          </div>
          <div>
            <div>行动时间</div>
            <div>{actionItemData.time}</div>
          </div>
          <div>
            <div>行动地点</div>
            <div>{actionItemData.address}</div>
          </div>
          <div>
            <div>行动分类</div>
            <div>{actionItemData.actionCatogory}</div>
          </div>
          <div>
            <div>行动状态</div>
            <div>{actionItemData.status}</div>
          </div>
          <div>
            <div>跟踪记录</div>
            <div>{actionItemData.trackRecord}</div>
          </div>
        </div>
    </div>
  );
};
const ReleaseActionWrapper = createForm()(ReleaseAction);
export default ReleaseActionWrapper;
